import React, { Component } from 'react';
import Btn from './ShadowButton'
import './Calculator.scss'

class Calculator extends React.Component{
    constructor(props){
        super(props);
        this.state={
            inputNum:0,   //ì…ë ¥ë°›ì€ ê°’ (ì‹¤ì œ ê³„ì‚°ì¬ë£Œì´ë‹ˆ â˜…ì •í™•íˆ ìˆ«ìë¡œ)
            process:"",   //ì „ì²´ê³¼ì • (í™”ë©´1)
            current:"",   //í˜„ì¬ê³¼ì • (í™”ë©´2)
            result:0,     //ì—°ì‚°ê¸°í˜¸ë¥¼ ëˆ„ë¥¸ì‹œì ê¹Œì§€ì˜ ê²°ê³¼ (ì‹¤ì œ ê³„ì‚°ì¬ë£Œì´ë‹ˆ â˜…ì •í™•íˆ ìˆ«ìë¡œ)
            operator:"",  //ì—°ì‚°ì 
            setNum:false, //ì—°ì‚°ê¸°í˜¸ë¥¼ ì‚¬ìš©í•œ ë’¤ ìµœì´ˆì˜ ìˆ˜ì´ë‹ˆ? true --> current ì´ˆê¸°í™”
            restart:false //'ê¸°í˜¸ =' ì´í›„ ì²« ìˆ˜(ë˜ëŠ” ì²« ê¸°í˜¸)ì´ë‹ˆ?
        }
    }

    //--------------------------- ëª¨ë“  ë²„íŠ¼ì—ì„œ ì‘ë™í•˜ëŠ” í•¨ìˆ˜(ê·¼ë° ì´ê±° êµ³ì´...?) -----------------------------

    btnHandler(e){ //ë¦¬ì•¡íŠ¸ì—ì„œëŠ” eë§Œìœ¼ë¡œë„ eventë¥¼ ë°›ì•„ì˜¨ë‹¤
        let val=e.target.value;
        let className=e.target.className;

        switch(className){ //ìŠ¤ìœ„ì¹˜ë¥¼ ì´ìš©í•˜ì—¬ classNameì— ë§ëŠ” í•¨ìˆ˜ê°€ ì‹¤í–‰ë˜ë„ë¡ í•œë‹¤.
           case "num" : this.numHandler(val); break;
           case "operator" : this.operatorHandler(val); break;
           case "selfOperator" : this.selfOperatorHandler(val); break;
           case "control" : this.controlHandler(val); break; //defaultëŠ” ê± ìƒëµí•´ë„ ë˜ëŠ”êµ°
        }

        console.log("current: "+this.state.current);//í•¨ìˆ˜ê°€ ë‹¤ ì¢…ë£Œë˜ì–´ì•¼ ë³€í™”ëœ stateê°’ì´ ì ìš©
        console.log("input: "+this.state.inputNum); //ì´ ì‹œì ì—ì„œëŠ” ìµœì¢… ë³€í™”ëœ ê°’ì„ ëª»ì½ìŒ.
    }

    //---------------------------------- ìˆ«ì ë²„íŠ¼ --------------------------------------
    numHandler(num){
        //-------------------- í›„ì— stateê°’ìœ¼ë¡œ ëŒ€ì…í•  ë³€ìˆ˜ë“¤ë¶€í„° ìƒì„± ---------------------
        let setNum=this.state.setNum;
        let current=""; //ì¼ë‹¨ ëŠ˜ ìƒˆë¡œìš¸ ì˜ˆì •ì¸ currentëŠ” ë¹„ì›Œ ë†“ê³ 
        let restart=this.state.restart;
        console.log(this.state.restart); //= ë‹¤ìŒ ì²« ìˆ˜, ì¦‰ ì—¬ê¸°ì„œ ë¹„ë¡œì†Œ trueë¡œ ë°”ë€ê²ƒì„ í™•ì¸

        if(setNum){
            current+=num; //ê¸°í˜¸ ì‚¬ìš© í›„ ì²« num ì´ë¼ë©´ currentì— numë§Œì„ ë„£ê³ 
            setNum=false; //trueë¡œ ê¸°ëŠ¥ì„ ì“´ í›„ì—ëŠ” falseë¡œ ì „í™˜í•´ì¤˜ì•¼ì§€
        }else{
            current=this.state.current+num; //ì•„ë‹ë•? ê¸°ì¡´ current(ìˆ«ìê² ì§€)ì— numì„ ë”í•´ì„œ current 'ë¬¸ìì—´'ì„ ëŠ˜ë ¤ê°€ì§€
        }

        this.setState(
            {
                current: current,
                inputNum: Number(current), //ë¬¸ìì—´ë¡œ ë°›ì•„ë‘” currentë¥¼ ìˆ«ìë¡œ í˜•ë³€í™˜ í›„ inputNumì˜ ê°’ìœ¼ë¡œ
                process: `${this.state.process+num}`, 
                setNum: setNum,
            });

        if(restart){
            this.setState({
                process:num,    //processë¥¼ ì‹¹ ë¹„ìš°ê³  ìƒˆë¡œ ì…ë ¥í•˜ëŠ” ìˆ«ìë§Œ ë„£ëŠ”ë‹¤.
                restart:false,  //trueë¡œ ê¸°ëŠ¥ì„ ì“´ í›„ì—ëŠ” falseë¡œ ì „í™˜í•´ì¤˜ì•¼ì§€
            })
        }
    }

    //-------------------------------- ì´í•­ ì—°ì‚°ì ë²„íŠ¼ --------------------------------------

    operatorHandler(operator){
    //operator ëˆ„ë¥¸ ì‹œì ê¹Œì§€ì˜ ì—°ì‚°ê²°ê³¼ë¥¼ ë‚´ëŠ” ì¬ë£Œ: â˜… result, operator, inputNum â˜…

    //-------------------- ì—­ì‹œ stateê°’ìœ¼ë¡œ ëŒ€ì…í•  ë³€ìˆ˜ë“¤ë¶€í„° ìƒì„± ---------------------
        let result=this.state.result;
        let inputNum=this.state.inputNum;
        let current=this.state.current;
        let process=this.state.process;
        let restart=this.state.restart;

        if(this.state.restart){       // ëŠ˜ restartê°€ trueì¼ê²½ìš°ëŠ” ë”°ë¡œ ê¸°ëŠ¥check
            process=result;           //â˜…â˜…â˜… stateì˜ ë³€ê²½ì€ í•¨ìˆ˜ê°€ ì¢…ë£Œë  ë–„ ì ìš©ëœë‹¤ëŠ” ì  ì£¼ì˜
            restart=false;            //trueë¡œ ê¸°ëŠ¥ì„ ì“´ í›„ì—ëŠ” falseë¡œ ì „í™˜í•´ì¤˜ì•¼ì§€
        }

        //ê¸°í˜¸ë¥¼ ëˆ„ë¥´ëŠ” ìˆœê°„ ë¬µí˜€ë’€ë˜ ì´ì „ì˜ ì—°ì‚°ì„ ì‹¤í–‰í•œë‹¤. ( result operator inputNum )
        switch(this.state.operator){ //this.state.operator(ì´ì „ ì…ë ¥ê¸°í˜¸) != operator(í˜„ì¬ ì…ë ¥ê¸°í˜¸)
            case "" : //ì €ì¥ëœ ê¸°í˜¸ this.state.operator ê°€ ì—†ì„ ë•Œ (ì¦‰ ì²« ì´í•­ì—°ì‚° ì‚¬ìš© ì‹œ)
                result=inputNum; //ê¸°ì¡´ currentì— í‘œì‹œë˜ë˜ inputNum(ì •í™•í•œ ìˆ«ì)ì„ ë°›ì•„ì˜´.
                break;
            case "Ã·" :
                result/=inputNum;
                break;
            case "Ã—" : 
                result*=inputNum;
                break;
            case "âˆ’" : 
                result-=inputNum;
                break;
            case "+" : 
                result+=inputNum;
                break;
            case "Â±" :  break; //ê±..
        }

        process=process+operator; 
        
        const stateObj={    // <<-- ì—¬ê¸°ì„œ êµ³ì´ ê°ì²´ë¥¼ ë§Œë“¤ì–´ì„œ ëŒ€ì…í•˜ëŠ” ì´ìœ ëŠ”?? ë°‘ì—..
            result : result, //ìœ„ì—ì„œ ì—°ì‚°ëœ ë°”ë€ resultê°’ìœ¼ë¡œ ì—…ë°ì´íŠ¸
            current: result, //ì´ì „ ê¸°í˜¸ê¹Œì§€ ê³„ì‚°ëœ ê²°ê³¼ê°€ í™”ë©´2ì— ë‚˜ì™”ìœ¼ë©´ í•´
            process: process,
            setNum: true, //ê¸°í˜¸ë¥¼ ì“°ê³ ë‚˜ë©´ setNumì„ trueë¡œ ì¤˜ì•¼ ë‹¤ìŒ Numì´ ìµœì´ˆê°€ ë˜ì§€
            operator: operator, //operatorì•ˆì—ëŠ” ì´ë²ˆì— ëˆ„ë¥¸ ì—°ì‚°ê¸°í˜¸ë¥¼ in
            restart: restart,
        }
        this.setState(stateObj);
        return stateObj; // â˜…â˜…â˜…â˜…  ì´ë ‡ê²Œ returnê°’ì„ ë°˜í™˜í•˜ë„ë¡ í•¨ìˆ˜ë¥¼ ë§Œë“¤ë©´(ì„¤ê³„í•˜ëŠ” ì´ìœ )?  â˜…â˜…â˜… 
                         // ë‹¤ë¥¸ ê³³ì—ì„œ operatorHandlerë¥¼ ì‹¤í–‰ì‹œí‚¬ ë•Œ ë¦¬í„´ë˜ëŠ” stateObjì„ ë°›ì„ ìˆ˜o
                         // ì¦‰ ì´ í•¨ìˆ˜ë¥¼ ì‹¤í–‰í•˜ë©° ì‚°ì¶œë˜ëŠ” ì •ë³´ë“¤ì„ ë‹¤ë¥¸ ê³³ì—ì„œë„ ì“¸ ìˆ˜ o
    }

    selfOperatorHandler(selfOper){
        
        let process=this.state.process; 
        let result=this.state.result;
        let current=this.state.current;
        let inputNum=this.state.inputNum;
        let restart=this.state.restart;
        
        let i= this.state.process.lastIndexOf(this.state.inputNum); 

        //ë‹¤ìŒì„ ë‹¤ì¤‘ ifêµ¬ë¬¸ìœ¼ë¡œ ë°˜ë³µì½”ë“œë¥¼ ì¤„ì¼ ìˆ˜ë„ ìˆì„ ê²ƒ ê°™ê¸´í•œë°.. ì¼ë‹¨ ì—¬ê¸°ì„œ stop..

        switch(selfOper){

            case "âˆš" :   //--------------------------- case 1 --------------------------------

            inputNum=Math.sqrt(inputNum);

            if(this.state.restart){  //'='ì´í›„ ë°”ë¡œ ë£¨íŠ¸(ë‹¤ë¥¸ ë†ˆë“¤ë„ ë§ˆì°¬ê°€ì§€)ë¥¼ ëˆ„ë¥¸ ê²½ìš°ë¼ë©´
                process=inputNum;    //processë¥¼ ê³„ì‚°ëœ inputNumìœ¼ë¡œ ë°”ê¾¸ê³ 
                result=inputNum;     //result ì—­ì‹œ inputNumìœ¼ë¡œ ë„£ì–´ë‘¬ì•¼ì§€
            }else{
                process= this.state.process.substr(0,i); // ê¸°ì¡´ inputNumì„ ì œì™¸í•˜ê³ !!!!!!!!
                process += `${inputNum}`;                // selfOperë¡œ ê³„ì‚°ëœ inputNumì„ ëŒ€ì‹  ë„£ëŠ” ì‘ì—…
            }            
            break;

            case "ğ’³Â²" :  //--------------------------- case 2 --------------------------------

            inputNum=inputNum*inputNum;
            if(this.state.restart){
                process=inputNum;
                result=inputNum;
            }else{
                process= this.state.process.substr(0,i); // â˜…â˜… ê¸°ì¡´ inputNumì„ ì œì™¸í•˜ê³  â˜…â˜…
                process += `${inputNum}`;   
            }
            //process=`${this.state.process.substr(0,i)}(${inputNum})Â²`; //= ì´í›„ ì¢€ ì´ìƒí•˜ê²Œ ëŒì•„ê°€ë˜ ì½”ë“œ..
                break;

            case "Â¹/ğ’³" : //--------------------------- case 3 --------------------------------

            inputNum=1/inputNum;
            if(this.state.restart){
                process=inputNum;
                result=inputNum;
            }else{
                process= this.state.process.substr(0,i); // â˜…â˜… ê¸°ì¡´ inputNumì„ ì œì™¸í•˜ê³  â˜…â˜…
                process += `${inputNum}`;   
            }
            // process=`${this.state.process.substr(0,i)}(1/${inputNum})`; // = ì´í›„ ì¢€ ì´ìƒí•˜ê²Œ ëŒì•„ê°€ë˜ ì½”ë“œ
                break;

            case "=" :  //--------------------------- case 4 --------------------------------

            // ì„¤ê³„ Idea
            // ì˜ˆë¥¼ë“¤ì–´ 2+3= ê°™ì€ê²½ìš°ë¥¼ ë³´ë©´?
            // ì•„ì§ ë‘ë²ˆ ì§¸ operatorê°€ ëˆŒë¦¬ì§€ X ë•Œë¬¸ì— ê³„ì‚°ë˜ì§€ ì•Šì€ ìƒíƒœ, ì¦‰ ì´ë•ŒëŠ” result=2
            // =ë¥¼ ëˆ„ë¥´ëŠ” ìˆœê°„ ê³„ì‚°ì´ ì§„í–‰ë˜ê³  ì§„í–‰ëœ resultë¥¼ ì–»ê¸°ë¥¼ ë°”ë¼ë¯€ë¡œ ë‹¤ìŒê³¼ ê°™ì´~!!!
            // (ìŒ.. '='ëŠ” ì˜ë¯¸ì ìœ¼ë¡œëŠ” selfì—°ì‚°ì ê°™ì§€ë§Œ ê¸°ëŠ¥ì ìœ¼ë¡œëŠ” ì´í•­ì—°ì‚°ìì— ë„£ëŠ”ê²Œ í¸í–ˆì„ìˆ˜ë„?)

            if(this.state.restart){ // '=' ëˆ„ë¥¸ ì§í›„ ë˜ = ëˆ„ë¥´ëŠ”ê±´ ì°¨ë‹¨
                break;
            }
            const stateObj=this.operatorHandler(this.state.operator); //operatorHandlerë¥¼ ì‹¤í–‰ & stateObj get
            console.log("--------------stateObj---------------");
            console.log(stateObj); //ì´ë†ˆì—ì„œ í•„ìš”í•œ ê²ƒì€ only result
            console.log(result);            //result : resultë¡œ í‚¤í•‘í•´ë‘” ê°’(ì¦‰ ìµœì¢…ê³„ì‚°ë˜ì§€ ì•Šì€ ê°’)
            console.log(stateObj.result);   //stateObj.result : í‚¤ë¥¼ ëˆ„ë¥´ë©° ìµœì¢… ê³„ì‚°ëœ ê°’(by operatorHandler)

            result=stateObj.result;
            process=`${process}=${result}`;
            restart=true;
                break;
        }

        if(selfOper == "="){
            this.setState({
                result:result,
                current: result,
                operator: "",
                process:process,
                restart: restart,
                setNum: true, //ê³ ë ‡ì§€ ì‹ ê²½ ì¨ì¤˜ì•¼ì§€
                inputNum:result, // '=' ì´í›„ ë°”ë¡œ operatorë¥¼ ëˆ„ë¥´ëŠ” ê²½ìš°ë¥¼ ì¶”ì í•´ë³´ë©´.. í•„ìš”í•˜êµ¬ë§Œ!!!!
            })
        }else{
        this.setState(
                {
                    result : result,
                    current: inputNum,
                    inputNum : inputNum,
                    process: process, 
                }
            );
        }
    }

    controlHandler(control){
        let i= this.state.process.lastIndexOf(this.state.inputNum); 
        console.log(i);
        switch(control){
            case "ce" :
                this.setState({
                    current:"",//í™”ë©´ì— í‘œì‹œë˜ëŠ” ê°’
                    inputNum:0, //ì…ë ¥ë°›ì€ ê°’
                    process: this.state.process.substr(0,i)
                })
                break;
                case "c" :
                this.setState({ // ê± ì´ˆê¸°í™”
                    current:"",
                    inputNum:0,
                    result:0,
                    process:"",
                    operator:"",
                    setNum:false
                });    
                break;
                case "back" :
                    let j= this.state.current.length;
                    let k= this.state.process.length;

                    console.log(this.state.inputNum);
                    console.log(this.state.inputNum.toString());

                    let inputNum=this.state.inputNum.toString()
                    let l= inputNum.length;
                    this.setState({
                    inputNum: Number(inputNum.substr(0,l-1)),
                    process: this.state.process.substr(0,k-1),
                    current:this.state.current.substr(0,j-1), //ë¬¸ìì—´ì˜ index=0ë¶€í„° index=i-1ê¹Œì§€ ë°˜í™˜
                }); 
            break;
         }
    }
    
    render(){
        return(
            <>
                <h2> Calculator </h2>
                <hr></hr>
                <div className="calculator">
                    <div className="tas" style={{display: "flex", flexDirection:"column"}}>
                        <div>
                            <textarea cols="20" rows="2" value={this.state.process} style={{textAlign: "right"}}/>
                        </div>
                        <div>
                            <textarea cols="20" rows="2" value={this.state.current} style={{textAlign: "right"}}/>
                        </div>
                    </div>

                    <div className="btns" style={{display: "grid", width:"200px", gridTemplateColumns: "auto auto auto auto", gridGap: "10px"}}>
                        <Btn className="selfOperator" value="âˆš" text="âˆš"
                        onClick={(e)=>this.btnHandler(e)}></Btn>
                        <Btn className="selfOperator" value="ğ’³Â²" text="ğ’³Â²"
                        onClick={(e)=>this.btnHandler(e)}></Btn>
                        <Btn className="selfOperator" value="Â¹/ğ’³" text="Â¹/ğ’³"
                        onClick={(e)=>this.btnHandler(e)}></Btn>
                        <Btn className="num" value="" text=""
                        onClick={(e)=>this.btnHandler(e)}></Btn>
                        <Btn className="control" value="ce" text="CE"
                        onClick={(e)=>this.btnHandler(e)}></Btn>
                        <Btn className="control" value="c" text="C"
                        onClick={(e)=>this.btnHandler(e)}></Btn>
                        <Btn className="control" value="back" text="â†"
                        onClick={(e)=>this.btnHandler(e)}></Btn>
                        <Btn className="operator" value="+" text="+"
                        onClick={(e)=>this.btnHandler(e)}></Btn>
                        <Btn className="num" value="1" text="1" 
                        onClick={this.btnHandler.bind(this)}></Btn>
                        <Btn className="num" value="2" text="2"
                        onClick={(e)=>this.btnHandler(e)}></Btn>
                        <Btn className="num" value="3" text="3"
                        onClick={(e)=>this.btnHandler(e)}></Btn>
                        <Btn className="operator" value="âˆ’" text="âˆ’"
                        onClick={(e)=>this.btnHandler(e)}></Btn>
                        <Btn className="num" value="4" text="4"
                        onClick={(e)=>this.btnHandler(e)}></Btn>
                        <Btn className="num" value="5" text="5"
                        onClick={(e)=>this.btnHandler(e)}></Btn>
                        <Btn className="num" value="6" text="6"
                        onClick={(e)=>this.btnHandler(e)}></Btn>
                        <Btn className="operator" value="Ã·" text="Ã·" 
                        onClick={(e)=>this.btnHandler(e)}
                        ></Btn>
                        <Btn className="num" value="7" text="7"
                        onClick={(e)=>this.btnHandler(e)}></Btn>
                        <Btn className="num" value="8" text="8"
                        onClick={(e)=>this.btnHandler(e)}></Btn>
                        <Btn className="num" value="9" text="9"
                        onClick={(e)=>this.btnHandler(e)}></Btn>
                        <Btn className="operator" value="Ã—" text="x"
                        onClick={(e)=>this.btnHandler(e)}></Btn>
                        <Btn className="num" value="" text=""
                        onClick={(e)=>this.btnHandler(e)}></Btn>
                        <Btn className="num" value="0" text="0"
                        onClick={(e)=>this.btnHandler(e)}></Btn>
                        <Btn className="num" value="" text=""
                        onClick={(e)=>this.btnHandler(e)}></Btn>
                        <Btn className="selfOperator" value="=" text="="
                        onClick={(e)=>this.btnHandler(e)}></Btn>
                    </div>
                </div>
            </>
        )
    }
}
export default Calculator;